import React from "react";
import { Navbar, Container } from "react-bootstrap";
import clientLogo from "../images/waka-kotahi.png";
import stantecLogo from "../images/stantec.svg";
import styled from "styled-components";

const Title = styled.span`
    color: 	#00466A;
    font-size: 2rem;
    font-weight: bold;
    padding-left: 30px;
`;

export default function Navigation() {
  return (
    <Navbar
      variant="dark"
      expand="lg"
      style={{ backgroundImage: 'linear-gradient(to right, Gainsboro, Gainsboro,Gainsboro, #00466A, #00466A)', zIndex: "100" }}
    >
      <Container fluid>
        <Navbar.Brand>
          <div>
          <img
            src={clientLogo}
            height={45}

            alt="Waka Kotahi"
          />
          <span><Title>Aspiring Highways - Corridor Access Requests</Title></span>

          
          </div>
        </Navbar.Brand>
        <span className="float-end"><img src={stantecLogo} height={35} alt="Stantec logo"/></span>
      </Container>
    </Navbar>
  );
}